function welcomeSection(THREE, gui, OrbitControls, canvas, scene, sizes ) {
    const textureLoader = new THREE.TextureLoader()
    const welcomeMatcap = textureLoader.load('textures/matcaps/matcap-welcome.png')


    //cube
    const cube = new THREE.BoxGeometry(1, 1, 1)
    const material = new THREE.MeshMatcapMaterial({
        color: 0xffffff,
        roughness: 0.5,
        metalness: 0.5,
        matcap: welcomeMatcap
    });



    const cubeMesh = new THREE.Mesh(cube, material)
    cubeMesh.position.y = 0
    cubeMesh.position.z = -0.5
    cubeMesh.position.x = 2
    cubeMesh.rotation.x = -0.57
    cubeMesh.rotation.y = -0.49
    cubeMesh.rotation.z = -0.87
    scene.add(cubeMesh)

    const guiCubeFolder = gui.addFolder('Welcome - Cube')
    guiCubeFolder.closed = true
    guiCubeFolder.add(cubeMesh.position, 'x', -5, 5).name('Position X').step(0.5)
    guiCubeFolder.add(cubeMesh.position, 'y', -5, 5).name('Position Y').step(0.5)
    guiCubeFolder.add(cubeMesh.position, 'z', -5, 5).name('Position Z').step(0.5)
    //rotation
    guiCubeFolder.add(cubeMesh.rotation, 'x', -Math.PI, Math.PI).name('Rotation X').step(0.01)
    guiCubeFolder.add(cubeMesh.rotation, 'y', -Math.PI, Math.PI).name('Rotation Y').step(0.01)
    guiCubeFolder.add(cubeMesh.rotation, 'z', -Math.PI, Math.PI).name('Rotation Z').step(0.01)



    const torus = new THREE.TorusGeometry(0.5, 0.3, 16, 100)
    const torusMesh = new THREE.Mesh(torus, material)
    torusMesh.position.x = -0.5
    torusMesh.position.y = -1
    torusMesh.position.z = -2
    torusMesh.rotation.x = -0.1
    torusMesh.rotation.y = 0.67
    torusMesh.rotation.z = -0.02
    scene.add(torusMesh)

    const guiTorusFolder = gui.addFolder('Welcome - Torus')
    guiTorusFolder.add(torusMesh.position, 'x', -5, 5).name('Position X').step(0.5)
    guiTorusFolder.add(torusMesh.position, 'y', -5, 5).name('Position Y').step(0.5)
    guiTorusFolder.add(torusMesh.position, 'z', -5, 5).name('Position Z').step(0.5)
    //rotation
    guiTorusFolder.add(torusMesh.rotation, 'x', -Math.PI, Math.PI).name('Rotation X').step(0.01)
    guiTorusFolder.add(torusMesh.rotation, 'y', -Math.PI, Math.PI).name('Rotation Y').step(0.01)
    guiTorusFolder.add(torusMesh.rotation, 'z', -Math.PI, Math.PI).name('Rotation Z').step(0.01)

    const tetrahedron = new THREE.TetrahedronGeometry(0.5, 0)
    const tetrahedronMesh = new THREE.Mesh(tetrahedron, material)
    tetrahedronMesh.position.x = -1.5
    tetrahedronMesh.position.y = 1.5
    tetrahedronMesh.position.z = -2.5
    tetrahedronMesh.rotation.x = -0.13
    tetrahedronMesh.rotation.y = 0.67
    tetrahedronMesh.rotation.z = 0
    scene.add(tetrahedronMesh)

    const guiTetrahedronFolder = gui.addFolder('Welcome - Tetrahedron')
    guiTetrahedronFolder.add(tetrahedronMesh.position, 'x', -5, 5).name('Position X').step(0.5)
    guiTetrahedronFolder.add(tetrahedronMesh.position, 'y', -5, 5).name('Position Y').step(0.5)
    guiTetrahedronFolder.add(tetrahedronMesh.position, 'z', -5, 5).name('Position Z').step(0.5)
    //rotation
    guiTetrahedronFolder.add(tetrahedronMesh.rotation, 'x', -Math.PI, Math.PI).name('Rotation X').step(0.01)
    guiTetrahedronFolder.add(tetrahedronMesh.rotation, 'y', -Math.PI, Math.PI).name('Rotation Y').step(0.01)
    guiTetrahedronFolder.add(tetrahedronMesh.rotation, 'z', -Math.PI, Math.PI).name('Rotation Z').step(0.01)


    const sphere = new THREE.SphereGeometry(0.5, 16, 16)
    const sphereMesh = new THREE.Mesh(sphere, material)
    sphereMesh.position.x = -3.5
    sphereMesh.position.y = -4
    sphereMesh.position.z = -2
    scene.add(sphereMesh)

    const guiSphereFolder = gui.addFolder('Welcome - Sphere')
    guiSphereFolder.add(sphereMesh.position, 'x', -5, 5).name('Position X').step(0.5)
    guiSphereFolder.add(sphereMesh.position, 'y', -5, 5).name('Position Y').step(0.5)
    guiSphereFolder.add(sphereMesh.position, 'z', -5, 5).name('Position Z').step(0.5)

    return {
        tetrahedron: tetrahedronMesh,
        torus: torusMesh,
        cube: cubeMesh,
    }
    

}

export default welcomeSection;