function randomInteger(min, max) {
    return Math.floor(Math.random() * (max - min + 1)) + min;
  }

function how(THREE, gui, OrbitControls, canvas, scene, sizes ) {
    const textureLoader = new THREE.TextureLoader()
    const welcomeMatcap = textureLoader.load('textures/matcaps/matcap-whatis.png')

    

    //cube
    const material = new THREE.MeshBasicMaterial({
        color: 0xffffff,
        wireframe: true
    });


    let sphere = new THREE.SphereGeometry(5, 50, 50)
    let sphereMesh = new THREE.Mesh( sphere, material )

    sphereMesh.position.x = 7
    sphereMesh.position.y = -20
    sphereMesh.position.z = -5

    //add opacity to the sphere
    sphereMesh.material.transparent = true
    sphereMesh.material.opacity = 0.7
    
    scene.add(sphereMesh)

    let guiSphereFolder = gui.addFolder('How is - Sphere')
    guiSphereFolder.add(sphereMesh.position, 'x', -5, 5).name('Position X').step(0.5)
    guiSphereFolder.add(sphereMesh.position, 'y', -5, 5).name('Position Y').step(0.5)
    guiSphereFolder.add(sphereMesh.position, 'z', -5, 5).name('Position Z').step(0.5)

    return sphereMesh;

}

export default how;