import './style.css'
import './mobile-style.css'
import * as THREE from 'three'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'
import GUI from 'lil-gui'
import welcomeSection from './welcome'
import whatSection from './what'
import howSection from './how'

const gui = new GUI()

const canvas = document.querySelector('#webgl')

const scene = new THREE.Scene()
const sizes = {
    width: window.innerWidth,
    height: window.innerHeight
}
//if url ends with #debug, show debug gui
if (window.location.hash === '#debug') {
    gui.show()
} else {
    gui.hide()
}

//get elements that has .background class
const backgrounds = document.querySelectorAll('.background')
const firstBackground = backgrounds[0].style.opacity = 1
const currentBackgroundIndex = 0;

// Camera
const camera = new THREE.PerspectiveCamera(50, sizes.width / sizes.height, 0.1, 100)
camera.position.x = 1
camera.position.y = 1
camera.position.z = 2

const handleCameraPositionOnScroll = ( scrollY ) => {
    setTimeout(function(){
        camera.position.y = scrollY * -0.01;
    }, 10 )
    //camera.position.y = scrollY * -0.01;
}

const handleOnScroll = ( ) => {
    //get the scrollY from the top of the page
    const scrollY = window.scrollY
    handleCameraPositionOnScroll( scrollY )
    //get the viewport height
    const viewportHeight = window.innerHeight / 1.2
    const scrollYByViewportHeight = (scrollY / viewportHeight);
    //console.log('scrollY by viewport', scrollYByViewportHeight )
    //get only the integer part of the scrollY divided by the viewport height
    const section = Math.floor(scrollYByViewportHeight)
    //select the element that has the same index as the section
    const background = backgrounds[section]

    //if the element exists, set the opacity to 1
    if (background) {
        background.style.opacity = 1
        //all other elements opacity to 0
        for (let i = 0; i < backgrounds.length; i++) {
            if (i !== section) {
                backgrounds[i].style.opacity = 0
            }
        }
    }
}

//on scroll, check the current visible section and set data-background opacity to 1
window.addEventListener('scroll', () => {
    handleOnScroll()
})


scene.add(camera)
handleOnScroll()
window.addEventListener('resize', () => {
    // Update sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight

    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()

    // Update renderer
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
})

const welcomeMeshes = welcomeSection(THREE, gui, OrbitControls, canvas, scene, sizes)
const tetrahedronMeshs = whatSection(THREE, gui, OrbitControls, canvas, scene, sizes)
const sphereMesh = howSection(THREE, gui, OrbitControls, canvas, scene, sizes)


/**
     * Renderer
     */
const renderer = new THREE.WebGLRenderer({
    canvas: canvas,
    antialias: true,
    alpha: true
})
renderer.setSize(sizes.width, sizes.height)
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))

/**
 * Animate
 */
const clock = new THREE.Clock()

const tick = () => {
    const elapsedTime = clock.getElapsedTime()

    //slow x rotation using elapsed time
    sphereMesh.rotation.y = 0.2 * (elapsedTime / 2)
    tetrahedronMeshs.forEach( ( tetrahedronMesh ) =>{
        tetrahedronMesh.rotation.y = 0.3 * (elapsedTime / 2)
        //tetrahedronMesh.rotation.z = 0.3 * (elapsedTime / 2)
    })
    welcomeMeshes.cube.rotation.y = 0.3 * (elapsedTime / 2)
    welcomeMeshes.tetrahedron.rotation.y = 0.3 * (elapsedTime / 2)
    welcomeMeshes.torus.rotation.y = 0.3 * (elapsedTime / 2)

    // // Update controls
    // controls.update()

    // Render
    renderer.render(scene, camera)

    // Call tick again on the next frame
    window.requestAnimationFrame(tick)
}

tick()